.container {
  max-width: 1100px;
  margin: 0 auto;
  overflow: auto;
  padding: 0 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
}
.wrapper {
  background-color: white;
  width: 60%;
  margin-top: 150px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}
.wrapper h3 {
  font-family: "Lato", sans-serif;
  font-weight: 700;
  font-size: 30px;
  margin-bottom: 25px;
  margin-top: 10px;
}

form {
  display: flex;
  justify-content: center;
  width: 100%;
  margin-bottom: 20px;
}
.inputBox {
  width: 67%;
  outline: none;
  border: none;
  background-color: #f1f5f8;
  text-indent: 10px;
  font-family: "Lato", sans-serif;
  font-weight: 700;
}
.inputBox:focus {
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.btn {
  padding: 7px 30px 7px 30px;
  background-color: #a5d5f8;
  border: none;
  outline: none;
  color: black;
  font-family: "Lato", sans-serif;
  font-size: 15px;
  font-weight: 700;
  border-radius: 2px;
  cursor: pointer;
  transition: 0.3s ease-in-out;
}
.btn:hover {
  transition: 0.3s ease-in-out;
  transform: scale(1.2);
}
.displayContainer {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.displayList {
  border-radius: 3px;
  width: 84%;
  margin-left: 48px;
  padding-bottom: 3px;
  padding-top: 3px;
  background-color: #aab7b8;
  display: flex;
  color: white;
  margin-bottom: 20px;
  flex-wrap: wrap;
}
.displayList:hover {
  background-color: #5499c7;
  color: white;
}

.displayText {
  font-size: 15px;
  text-indent: 10px;
  color: black;
  font-family: "Lato", sans-serif;
}
.displayText:hover {
  color: #f1f5f8;
}
.iconContainer {
  display: flex;
  cursor: pointer;
}
.edit {
  color: green;
  margin-top: 3px;
  font-size: 20px;
  transition: 0.3s ease-in-out;
}
.edit:hover {
  transition: 0.3s ease-in-out;
  transform: scale(1.2);
  color: white;
}
.delete {
  margin-left: 5px;
  color: #e66b6b;
  font-size: 23px;
  transition: 0.3s ease-in-out;
}
.delete:hover {
  transition: 0.3s ease-in-out;
  transform: scale(1.2);
  color: red;
}
.textContainer {
  width: 90%;
}

.hideInput {
  display: none;
}
.addedList1 {
  transition: 0.2s ease-in-out;
  transform: scale(0);
}
.addedList {
  width: 90%;
  border-radius: 5px;
  margin-top: 15px;
  padding-bottom: 5px;
  padding-top: 5px;
  background-color: #abebc6;
  transition: 0.2s ease-in-out;
  transform: scale(1);
}
.addedTexth4 {
  color: black;
  font-family: "Lato", sans-serif;
  font-size: 12px;
  text-align: center;
}
.btnClear {
  margin-bottom: 25px;
  color: #a93226;
  background-color: white;
  border: 2px solid #a5d5f8;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}
.btnClear:hover {
  box-shadow: none;
}

/* Tablet and under */
@media (max-width: 768px) {
  .wrapper {
    width: 100%;
  }
}
/* Mobile */
@media (max-width: 768px) {
  .wrapper {
    width: 90%;
  }
  .inputBox {
    width: 50%;
  }
  .displayContainer {
    width: 70%;
    display: flex;
    flex-direction: column;
  }
  .displayList {
    width: 100%;
    margin-left: 0px;
  }
  .textContainer {
    width: 80%;
  }
}
/* Snall screen Mobile*/
@media (max-width: 400px) and (min-width: 320px) {
  .wrapper {
    width: 100%;
  }
  .inputBox {
    width: 68%;
  }
  .btn {
    padding: 5px 15px 5px 15px;
    font-size: 10px;
  }
  .displayContainer {
    width: 100%;
  }

  .container h3 {
    font-size: 25px;
  }
}
